import React from "react";
import "./header.scss";
import img from "../../images/Headerimg.JPG";

const Header = () => {
  return (
    <div className="header">
      <div className="container">
        <div className="display-div">
            <div className="left">
              <h1>
                Освойте новую <br /> профессию и <br /> улучшите качество <br />
                жизни
              </h1>
              <div className="bottom">
                <div className="start">
                  <button>Связаться с нами</button>
                  <a href="##">Записаться на пробный урок</a>
                </div>
              </div>
            </div>
            <div className="right">
              <img src={img} alt="Header_image" />
            </div>
        </div>
      </div>
    </div>
  );
};

export default Header;
