import React from 'react'
import "./Info.scss"
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import HomeWorkIcon from '@mui/icons-material/HomeWork';

const Info = () => {
    return (
        <div>
            <div className="container">
                <div className="info">
                    <div className="info-first">
                        <h6><span><AccessTimeIcon /></span> Продолжительность</h6>
                        <p>За каждый paper
                            3 месяца</p>
                    </div>
                    {/* <div className='sex'><div className='border'></div></div> */}
                    <div className="info-second">
                        <h6>В портфолио</h6>
                        <p>12  проекта</p>
                    </div>
                    {/* <div className="sex"><div className='border'></div></div> */}
                    <div className="info-third">
                        <h6><span><HomeWorkIcon /></span>Поддержка в</h6>
                        <p>трудоустройстве</p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Info