import React from "react";
import "./cources.scss";
import data from "./courcesData";

const Cources = () => {
  return (
    <div className="cources">
      <div className="container">
        <div className="c_content">
          <h1>Наши курсы</h1>
          <div className="cards">
            <div className="cardo">
              <div className="c_contents">
                <h2 className="acca_name">ACCA</h2>
                <p className="acca_p">
                  ACCA -это глобальная ассоциация профессионалов в области
                  финансов, учета и аудита, регулируемая королевской хартией.
                </p>
                <h3 className="acca_h3">От F1 До F3 Paper</h3>
                <p className="acca_pr">2.700000/мес каждый paper по 3 месяца</p>
                <h3 className="acca_h3">От F4 До F9 Paper</h3>
                <p className="acca_pr">3.100000/мес каждый paper по 3 месяца</p>
                <button className="button">Подробнее</button>
              </div>
            </div>
            <div className="cardo">
              <div className="c_contents">
                <h2 className="acca_name">FRONT-END</h2>
                <p className="acca_p">
                  FrontEnd разработчик создает видимую для пользователя часть
                  веб-страницы, реализовать пользовательскую логику.
                </p>
                <h3 className="acca_h3">7 месяца</h3>
                <p className="acca_pr">1.500000/мес</p>
                <button className="button">Подробнее</button>
              </div>
            </div>
            {data.map((el) => {
              return (
                <div className="card">
                  <div className="c_contents">
                    <h2 className="c_h2">{el.cource}</h2>
                    <p className="c_p">{el.content}</p>
                    <h3 className="c_h3">{el.science}</h3>
                    <p className="c_pr">{el.price}</p>
                    <h3 className="c_h3">{el.science2}</h3>
                    <p className="c_pr">{el.price2}</p>
                    <button className="button">Подробнее</button>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Cources;
