import React from 'react'
import "./FrontEndHeader.scss"
import frontendH from "../../images/frontendH.png"

const FrontEndHeader = () => {
    return (
        <div>
            <div className="container">
                <div className="front-header">
                    <div className="row">
                        <div className="col-md-6">
                            <div className="front-text">
                                <h1>FRONT END</h1>
                                <h6>
                                    FrontEnd разработчик создает видимую для пользователя часть веб-страницы и его главная задача
                                    точно передать в верстке то, что создал дизайнер, а также реализовать пользовательскую логику.
                                </h6>
                                <button>Хочу учиться</button>
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="front-img">
                                <div></div>
                                <img src={frontendH} alt="" />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default FrontEndHeader