import React from "react";
import "./Contact.scss";

const Contact = () => {
  return (
    <div className="contact">
      <div className="container">
        <div className="back">
          <div className="left">
            <h1>Записаться на курс или получить бесплатную консультацию</h1>
            <p>
              Если у вас есть вопросы о формате или вы не знаете, что выбрать,
              оставьте свой номер — мы позвоним, чтобы ответить на все ваши
              вопросы.
            </p>
          </div>
          <div className="right">
            <input className="name" placeholder="Ваше имя" type="name" />
            <div className="bottom">
              <input className="tel" placeholder="Телефон" type="name" />
              <div className="inputs">
                <select className="select" name="select">
                  <option value="ACCA">ACCA</option>
                  <option value="English General">English General</option>
                  <option value="Front end IT" selected>
                    Front end IT
                  </option>
                  <option value="Русский язык">Русский язык</option>
                </select>
              </div>
            </div>
            <button className="button">Записатся на курс</button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Contact;
