import React from "react";
import "./Footer.scss";
import logo from "../../images/Logo.svg";
import FacebookRoundedIcon from "@mui/icons-material/FacebookRounded";
import TelegramIcon from "@mui/icons-material/Telegram";
import InstagramIcon from "@mui/icons-material/Instagram";

const Footer = () => {
  return (
    <div>
      <footer>
        <div className="footer-content">
          <div className="first-footer">
            <img src={logo} alt="" />
            <h1>Почта: </h1>
            <h1>Связаться с нами: +998 88 701-22-29</h1>
            <h1>Адрес: Chilonzor, Andalus</h1>
            <h1>График работы: Пон-пят, 09:00-18:00</h1>
          </div>

          <div className="sec-footer">
            <h1>Навигация</h1>
            <h6>Главная</h6>
            <h6>О нас</h6>
            <h6>Новости</h6>
            <h6>Вакансии</h6>
            <h6>Контакты</h6>
          </div>

          <div className="third-footer">
            <h1>Курсы</h1>
            <h6>ACCA</h6>
            <h6>Разработка Frontend</h6>
            <h6>Math</h6>
            <h6>IELTS</h6>
            <h6>General English</h6>
          </div>

          <div className="fourth-footer">
            <div>
              <FacebookRoundedIcon />
            </div>
            <div>
              <TelegramIcon />
            </div>
            <div>
              <InstagramIcon />
            </div>
          </div>
        </div>
      </footer>
    </div>
  );
};

export default Footer;
