import React from 'react'
import "./Cards.scss"
import accaCard from "../../images/accaCard.png"

const AccaCards = () => {
  return (
    <div>
      <div className="container">
        <div className="cards">
          <div className="row">
            <div className="col-md-6 col-lg-6 col-sm-6">
              <div className="first-card">
                <div className="first-card-child">
                  <div className="child-text">
                    <h1>Стоимость курса</h1>
                    <p>Самых лучших учеников мы возьмем на работу с гарантированно хорошей зарплатой </p>
                    <h6>2.700000 </h6>
                    <h6>сум/мес</h6>
                  </div>
                  <div className="child-img">
                    <img src={accaCard} alt="" />
                  </div>
                </div>
              </div>
            </div>
            <div className="col-md-6 col-lg-6 col-sm-6">
              <div className="second-card">
                <h1>Записаться на курс или получить бесплатную консультацию</h1>
                <div className="container">
                  <input type="text" placeholder='Ваше имя' />
                  <input type="text" placeholder='Номер телефона' />
                </div>
                <div className="second-card-btn">
                  <button>Готова</button>

                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default AccaCards